var callbackMapper = {
	map : function(fn) {
		if(typeof window.__FN_INDEX !== 'undefined' && window.__FN_INDEX !== null) {
			var proxyFnName = 'F' + window.__FN_INDEX;
			window.__PROXY_FN[proxyFnName] = fn;
		  	window.__FN_INDEX++;
			return proxyFnName;
		} else {
			throw new Error("Please initialise window.__FN_INDEX = 0 in index.js of your project.");
		}
	}
}

exports["showUI'"] = function(sc,screen) {
	return function() {
		var screenJSON = JSON.parse(screen);
		var screenName = screenJSON.tag;
		screenJSON.screen = screenName;
		var backPressEvent = {tag:screenName+"Abort"};
		if(screenName=="InitScreen") {
			screenJSON.screen = "INIT_UI";
		}
		window.__duiShowScreen(sc, screenJSON);
	};
};

exports["onBackPress'"] = function(cb) {
	return function() {
		window.handleBackPress = function() {
			window.cb = cb
			window.handleBackPress = null;
			cb()();
		};
	};
};

var a = function(success, err, method, url, body, headersRaw) {
	var successResponse = {};
	var headers = {};
	for(var i=0;i<headersRaw.length;i++){
		headers[headersRaw[i].field] = headersRaw[i].value;
	}
	var callback = callbackMapper.map(function(params) {
		if (params && params.length >= 3) {
			successResponse = {
				status: params[0],
				response: JSON.parse(atob(params[1]) || "{}"),
				code: parseInt(params[2])
			};
			success(JSON.stringify(successResponse))();
		} else {
			success({
				status: "failed",
				response: "{}",
				code: 50
			})();
		}
	});
	JBridge.callAPI(method, url, body, JSON.stringify(headers),callback);
};

var callAPIFn = function(error) {
	return function(success) {
		return function(request) {
			return function() {
				callAPIImpl(success, error, request.method, request.url, request.payload, request.headers);
			};
		};
	};
};

exports["callAPI'"] = callAPIFn;



